/* sticky footer */
html {
  height: 100%;
}

body {
  height: 100%;
}

@media print {
  .hidden-print {
    display: none !important;
  }
}

#page_container {
  min-height: 100%;
}

#page_content {
  padding-bottom: 20px;
}

.outer-gutter {
  padding: 0 0.9375rem;
}

.content-margin-top {
  margin-top: 12px;
}

#page_header_top_row {
  border-left: solid 1px #ccc;
  border-right: solid 1px #ccc;
  border-bottom: solid 1px #e6e6e6;
  background-color: #eceeef;
  min-height: 66px;
  padding-top: 6px;
  vertical-align: top;
}

.page-header {
  margin: 0;
  padding: 8px 0;
  display: inline-block;
}

.page-header h1 {
  margin: 0;
  font-size: 2rem;
  font-family: "Yanone Kaffeesatz";
  color: #550000;
  letter-spacing: 2px;
}

#logo {
  vertical-align: top;
}

#page_title {
  font-size: 180%;
}

#userInfo .sub-info {
  line-height: 1.2;
  font-size: 85%;
}

body > footer {
  clear: both;
  height: 20px;
  margin-top: -20px;
  position: relative;
  border: solid 1px #ccc;
  border-bottom: none;
  font-size: 80%;
  border-top-right-radius: 0.25rem;
  border-top-left-radius: 0.25rem;
  color: #373a3c;
  background-color: #eceeef;
  padding-top: 2px;
}

#breadcrumbs {
  border: solid 1px #ccc;
  border-top: solid 1px white;
  margin-bottom: 0.625rem;
  font-size: 100%;
  border-bottom-right-radius: 0.25rem;
  border-bottom-left-radius: 0.25rem;
  color: #373a3c;
  background-color: #eceeef;
  vertical-align: middle;
  padding-top: 0.75rem;
}

#breadcrumbs a {
  color: #373a3c;
  padding-bottom: 0.75rem;
}

#breadcrumbs .inner {
  margin-left: -1px;
}

#breadcrumbs nav {
  display: inline-block;
  padding: 0 1em 0 0;
  margin: 0;
  position: absolute;
}

#breadcrumbs nav:hover .menu {
  display: block;
}

#breadcrumbs nav .button {
  line-height: 0px;
  margin: 0;
  padding: 0;
}

#breadcrumbs nav .menu {
  display: none;
  min-width: 150px;
  font-size: 1rem;
  background-color: white;
  border: solid 1px #ccc;
  border-bottom-right-radius: 0.25rem;
  border-bottom-left-radius: 0.25rem;
  background-color: white;
  white-space: nowrap;
  z-index: 9998;
  position: relative;
  left: -12px;
}

#breadcrumbs nav .menu li {
  width: 100%;
  position: relative;
  display: block;
  vertical-align: middle;
}

#breadcrumbs nav .menu li:hover {
  background-color: #f5f5f5;
}

#breadcrumbs nav .menu li:hover > ul {
  display: inline-block;
}

#breadcrumbs nav .menu li i {
  display: inline-block;
  padding-top: 3px;
}

#breadcrumbs nav .menu a {
  width: 100%;
  display: inline-block;
  padding: 3px;
}

#breadcrumbs nav .menu a:hover {
  text-decoration: none;
}

#breadcrumbs nav .menu ul {
  z-index: 9999;
  margin: 0;
  padding: 4px 8px;
  list-style-position: inside;
  list-style-type: none;
}

#breadcrumbs nav .menu ul ul {
  min-width: 150px;
  margin-left: 0px;
  display: none;
  position: absolute;
  left: 132px;
  top: -3px;
  border-bottom-right-radius: 0.25rem;
  border-bottom-left-radius: 0.25rem;
  background-color: white;
}

#breadcrumbs .crumbs {
  margin-left: 2em;
  display: inline-block;
}

#breadcrumbs .crumbs ul {
  list-style-type: none;
  padding: 0px;
  margin: 0px;
}

#breadcrumbs .crumbs ul li {
  float: left;
  margin-left: 0.2em;
  margin-right: 0.2em;
}

#breadcrumbs .crumbs ul li.breadcrumb-spacer {
  font-size: 80%;
  padding-top: 2px;
}

#breadcrumbs .crumbs ul li:first-child {
  margin-left: 0;
}

.sticky-breadcrumbs {
  position: fixed;
  opacity: 1;
  top: 0;
  z-index: 500;
}

/**
 * Breadcrumb Menu
 */
.bread-menu {
  overflow: auto;
}

.bread-menu ul:first-child {
  max-height: 500px;
  overflow: auto;
}

.bread-menu li {
  margin-left: 0 !important;
  position: static;
}

.bread-menu a {
  padding: 0 !important;
}

.bread-menu a:hover {
  text-decoration: none;
